import React from "react";
import Logo from "../assets/logo.png";
import Background from "../assets/banner.jpg";
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Fade from '@mui/material/Fade';
import MenuIcon from '@mui/icons-material/Menu';

function Banner(){
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    return(
        <div>
            <header id="header">
                <div id="company-name">
                    <img 
                        src={Logo} 
                        alt="Xbillion Skills Lab Logo"
                        className="logo"     
                    />
                </div>
                <nav id="navigation-bar">
                    <div>
                        <Button
                            id="fade-button"
                            aria-controls="fade-menu"
                            aria-haspopup="true"
                            aria-expanded={open ? 'true' : undefined}
                            onClick={handleClick}
                        >
                            <MenuIcon className="hamburger-menu"/>
                        </Button>
                        <Menu
                            id="fade-menu"
                            MenuListProps={{
                            'aria-labelledby': 'fade-button',
                            }}
                            anchorEl={anchorEl}
                            open={open}
                            onClose={handleClose}
                            TransitionComponent={Fade}
                        >
                            <MenuItem onClick={handleClose}>Home</MenuItem>
                            <MenuItem onClick={handleClose}>About</MenuItem>
                            <MenuItem onClick={handleClose}>Contact</MenuItem>
                        </Menu>
                    </div>
                </nav>
            </header>
            <div>
                <img 
                    src={Background}
                    alt="background" 
                    className="banner-background"    
                />
            </div>
            <div id="banner-content">
                <div className="content">
                    <h1 className="main-text"><span className="green-gradient">21st century skills</span> are <span className="red-gradient">superpowers</span><br/>in the new world of work</h1>
                    <div className="gradient-line"></div>
                    <h2 className="subtext">Master them <span className="bold">and</span> take the <span className="blue-gradient">SMART</span> cut to success</h2>
                    <div className="banner-button">
                        <button className="btn highlighted">GET STARTED</button>
                        <button className="btn outlined">KNOW MORE</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Banner;
